@-webkit-keyframes spinner {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
  @keyframes spinner {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
  .load {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    background:rgba(249, 89, 236, 1) ;
    height: 100%;
    width: 100%;
    top: 0;
    position: fixed;
    z-index: 200000;
  }
  #spinner {
    min-width: 60px;
    min-height: 60px;
    border: 5px solid #ccffff;
    border-right: 5px solid #00eded;
    border-radius: 50%;
